.App {
  min-height: 100vh;
  background-image: linear-gradient(
    to top,
     #a18cd1 0%,
     #fbc23b 100%);
}

.title{
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  padding: 5vh;
  font-size: 40px;
  font-family: Arial, Helvetica, sans-serif;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar{
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}